const state = {
  activeIndex: localStorage.getItem('activeIndex') || "/home",
  menus: [{
      label: "首页",
      route: {
        path: "/home"
      }
    },
    {
      label: "文章搜索",
      route: {
        path: "/articleSearch"
      }
    },
    {
      label: "病虫防治",
      route: {
        path: "/bingchongArticle"
      }
    },
    {
      label: "栽培方法",
      route: {
        path: "/zaipeiArticle"
      }
    },
    {
      label: "审批",
      route: {
        path: "/Application"
      }
    },
    {
      label: "公告",
      route: {
        path: "/notice"
      }
    },
    {
      label: "发布文章",
      route: {
        path: "/ArticlePublish"
      }
    },
    {
      label: "文章管理",
      route: {
        path: "/ArticleManage"
      }
    },
    {
      label: "个人中心",
      route: {
        path: "/Mine"
      }
    }
  ]
}

const getters = {
  ActiveIndex(state) {
    return state.activeIndex
  },
  menus(state) {
    return state.menus
  },
}

const mutations = {
  setActiveIndex(state, index) {
    state.activeIndex = index
    localStorage.setItem('activeIndex', index)
  }
}


export default {
  state,
  getters,
  mutations
}